@media only screen and (min-width : 320px) {
	.content {
		padding:30px 0;
	}
	.col-padding {
		padding:30px 15px;
	}
	.archive-title {
		margin-top:0;
	}
	.article-content {
		margin-top:15px;
		margin-bottom:50px;
		box-sizing:border-box;
		.wrapper {
			position: relative;
			background:#fff;
			padding:60px 30px;
			box-shadow: 0 0 15px rgba(0,0,0,0.20);
		}
		.category-name {
			text-transform: uppercase;
		}
		.entry-title {
			margin:10px 0;
		}
		.byline {
			padding: 0 0 30px 0;
			margin: 1.63em 0;		
			border-bottom:$clouds solid 5px;
		}	
	}

	/* By Line */
	.byline {
		list-style-type: none;
		margin:0;
		padding:0;
		li {
			display: inline-block;
			margin:0;
			vertical-align: middle;
		}
		img {
			-moz-border-radius:50%;
		 	-webkit-border-radius:50%;
		 	border-radius:50%;
		 	margin-right: 10px !important;
		}
	}

	.entry-meta {
		width:100%;
		padding:15px 0 0 0 ;
		margin:0 0 30px 0;
		border-bottom:$clouds solid 5px;
		border-top:$peter-river solid 5px;

		ul {
			list-style-type: none;
			padding:0;
			li {
				font-size: 12px;
				text-align: left;
				margin-bottom:0;
				line-height: normal;
				font-weight: $font-normal;
			}
		}
		p {
			margin-bottom:15px;
			font-size:12px;
			span {
				font-weight: $font-semi-bold !important;
			}
		}
	}

	/* Marketo Sharing Icons */
	.marketo-share {
		position: absolute;
		right:13px;
		top:15px;
		.cf_actionElem {
			display:none !important;
		}
		.cf_widget .cf_socialicons .cf_socialicon {
			width:32px !important;
			height: 32px !important;
		}
		.cf_linkedin, .cf_twitter, .cf_facebook {
			background-image:none !important;
			position: relative !important;
		}
		.cf_linkedin {	
			background-color:#007bb6 !important;
		}
		.cf_twitter {
			background-color:#4099ff !important;
		}
		.cf_facebook {
			background-color:#3b5998 !important;
		}
		.cf_linkedin:before, .cf_twitter:before, .cf_facebook:before  {
			position: absolute;
			font-family: 'FontAwesome';
			color:#fff;
			font-size: 16px;
		}
		.cf_linkedin:before {
			top: 3px;
			left: 9px;
			content: "\f0e1";
		}
		.cf_twitter:before {
			top: 4px;
			left: 9px;
			content: "\f099";
		}
		.cf_facebook:before {
			top: 4px;
			left: 11px;
			content: "\f09a";
		}
	}



	.social-sharing {
		margin-bottom:30px;
		height:32px;
		overflow: hidden;
		position: relative;
		div.cf_widget {
			line-height: normal;
			position: absolute;
			top:0;
			left:0;
		}
		.cf_widget .cf_socialicons {
			padding:0 !important;
		}
		.cf_actionElem {
			display:none !important;
		}
		.cf_widget .cf_socialicons .cf_socialicon, .rss-button {
			width:32px !important;
			height: 32px !important;
			margin:0 !important;
		}
		.cf_linkedin, .cf_twitter, .cf_facebook, .rss-button {
			background-image:none !important;
			position: absolute !important;
			top:0;
		}
		.rss-button {
			background-color:$orange;
			left:102px;
			line-height: normal;
			a {
				position: absolute;
				top:0;
				left:0;
