			}
		}
		.rss-button:hover {
			background-color:lighten($orange, 10%);
		}
		.cf_linkedin {	
			background-color:#007bb6 !important;
			left:68px;
		}
		.cf_linkedin:hover {
			background-color: lighten(#007bb6, 10%) !important;
		}
		.cf_twitter {
			background-color:#4099ff !important;
			left:34px;
		}
		.cf_twitter:hover {
			background-color: lighten(#4099ff, 10%) !important;
		}
		.cf_facebook {
			background-color:#3b5998 !important;
			left:0;
		}
		.cf_facebook:hover {
			background-color: lighten(#3b5998, 10%) !important;
		}
		.cf_linkedin:before, .cf_twitter:before, .cf_facebook:before, .rss-button:before  {
			position: absolute;
			font-family: 'FontAwesome';
			color:#fff;
			font-size: 16px;
