	/* Pagination */

	.pagination {
		clear:both;
		position:relative;
		font-size:11px;
		line-height:normal;
		text-align: center;
		margin:0;
		width:100%;
		border-top:$clouds solid 5px;
		padding:30px 0;
		.pagination-wrapper {
			margin:0 auto;
			display: inline-block;
		}
		span, a {
			display:block;
			float:left;
			margin:0 1px;
			padding:10px 13px 0 13px;
			text-decoration:none;
			width:auto;
			color:#fff;
			background:$silver;
			text-transform: uppercase;
			height:32px;
		}
		a:hover{
			color:#fff;
			background: #3498db;
		}
		.current{
			padding:10px 13px 0 13px;
			background: $emerald;
			color:#fff;
		}
	}

	/* Featured Post */
	.featured {
		position: relative;
		height:400px;
		.container  {
			position: absolute;
			top:0;
			left:0;
			width:100%;
			height: 100%;
			z-index: 100;
			padding:30px;
		}
	}

	.featured-post {
		margin-bottom:30px;
		box-sizing: border-box;
		position: relative;
		background-color:$clouds;
		background-size:cover;
		background-position:center center;
		height:300px;
		.text * {
			color:white;
			margin:0;
		}
		.text {			
			width:90%;
			position: absolute;
			left:30px;
			top:30px;			
			z-index: 100;
			height:275px;
			.post-category {
				display: inline-block;
				text-transform: uppercase;
				font-size:12px;
				font-weight: $font-normal;
				padding:10px;
				line-height: normal;
				margin-bottom:10px;
			}
			h2 {
				margin-bottom:15px;
				a:hover {
					text-decoration: underline;
				}
			}
		}
		.link {
			position: absolute;
			top:0;
			left:0;
			width:100%;
			height: 100%;
			z-index: 50;
		}
		.byline {
			position:absolute;
			bottom:30px;
			left:30px;
			z-index: 20;
			li {
				color:white;
			}
			img {
				border:white solid 2px;
			}
		}
		.meta {
			position:absolute;
			bottom:30px;
			left:30px;
			z-index: 20;
			margin:0;
			color:white;
		}
	}

	.paged .featured-post, .archive .featured-post {
		display: none;
	}


	/* Author Description */

	.author-description {
		line-height: normal;
		margin:1.63em 0;
		background:$clouds;
		position: relative;
		padding:65px 5px 5px 5px;
		.headshot {
			text-align: center;
			position: absolute;
			top:30px;
			left:0;
			width:100%;
		}
		h5 {
			margin-bottom:15px;
		}
		p {
			font-size: 13px;
		}
		.box-white {
			padding:70px 30px 30px 30px;
		}

	}
	.avatar {
		-moz-border-radius:50%;
	 	-webkit-border-radius:50%;
	 	border-radius:50%;
	 	display: inline-block !important;
	}
	.quote {
		display:inline-block;
		border-top:$clouds solid 5px;
		border-bottom:$clouds solid 5px;
		padding:30px 30px 0 30px;
		margin-bottom:1.63em;
		p {
			font-style: italic;
			font-weight: $font-normal;
		}
	}
	.feature-image {
		min-height:500px;
		background-size: cover;
		background-repeat: no-repeat;
		background-position: center center;
		background-color: #222;
		width:100%;
		position:relative;
		h1 {
			margin:0;
			line-height: 38px;
		}
		.image-attribution {
			position: absolute;
			bottom:0;
			right:0;
			z-index: 2;
			font-size: 12px;
			padding:5px 10px;
			background-color:rgba(0,0,0,.5);
			color:$silver;
		}
	}
	.blog-feature-image {
		.text {
			margin-top:130px;
			max-height: 200px;
			h2 {
				margin-bottom:15px;
			}
			a:hover {
				color:white;
				text-decoration: underline;
			}
			.categories {
				font-size: 14px;
				text-transform: uppercase;
				font-weight: $font-light;
			}
		}
		ul.meta {
			list-style-type: none;
			margin:0;
			padding: 0;
			li {
				display: inline-block;
				vertical-align: middle;
				font-size: 14px;
				color:white;
				span {
					font-weight: $font-semi-bold;
				}
				img {
					border:white solid 2px;
					margin-right:5px;
				}
			}			
		}
		.link {
			position: absolute;
			top:0;
			left:0;
			width: 100%;
			height: 100%;
		}
	}
	.next-prev-posts {
		.thumb {
			position: relative;			
			line-height: normal;
			span {
				position: absolute;
				top:0px;
				left:0px;
				padding:3px 5px;
				background-color:$peter-river;
				color:white;
				font-size: 11px;
				text-transform: uppercase;
			}
		}
		.category-name {
			font-size: 12px;
			margin-bottom: 5px;
		}
		h5 {
			margin-bottom:5px;
		}
	}
	ul.category-list {
		list-style-type: none;
		padding:0;
		li {
			padding:15px;
			background-color:white;
			margin-bottom:5px;
			line-height: normal;
			border:lighten($silver, 15px) solid 2px;

			h4 {
				margin:20px 0 5px 0;
				font-weight: $font-light;
			}
			a {
				color:$gray-dark;
			}
			a:hover {
				color:$peter-river;
			}
			.meta {
				font-size: 14px;
				color:$asbestos;
				span {
					font-weight: $font-semi-bold;
				}
			}
		}
		li:first-child {
			padding:0;
			margin:0;
			border:none;
		}
		li:nth-child(2) {
			padding:0;
			border:none;
		}
		li:last-child {
		}
		.thumb {
			position: relative;
			.avatars {
				position: absolute;
				left:15px;
				bottom:-25px;
				img {
					border:white solid 2px;
				}
			}
		}
		.title {
			padding:15px 15px 30px 15px;
			border:lighten($silver, 15%) solid 2px;
		}
		.btn {
			color:white;
			width:100%;
			text-align: left;
			position: relative;
			padding: 6px 35px 6px 12px;
			i {
				font-size: 18px;
				position: absolute;
				top:7px;
				right:10px;
			}
		}
		.btn:hover {
			color:white;
		}
		.background-green-sea:hover {
			background-color:lighten($green-sea,10%);
		}
		.background-carrot:hover {
			background-color:lighten($carrot,10%);
		}
		.background-alizarin:hover {
			background-color:lighten($alizarin,10%);
		}
		.background-wisteria:hover {
			background-color:lighten($wisteria,10%);
		}
		.background-nephritis:hover {
			background-color:lighten($nephritis,10%);
		}
		.background-belize-hole:hover {
			background-color:lighten($belize-hole,10%);
		}
		.repeater {
			border:lighten($silver, 15%) solid 2px;
		}
	}
	.sidebar-list {
	    margin-bottom:30px;
	    h5 {
	        margin:0;
	        text-transform: uppercase;
	    }
	    li {
	        border-bottom:lighten($silver, 15%) solid 1px;
	        margin:0 !important;
	        padding: 10px 0;
	        line-height: normal;
	        font-weight: $font-normal;
	        font-size: 14px;
	    }
	    li:first-child {
	        border-top:$clouds solid 5px;
	    }
	    li:last-child {
	        border:none;
	    }
	    a {
	        font-weight: $font-normal;
	        font-size: 14px;
	    }
	    .article-img-container {
	        display:inline-block;
	        width:20%;
	        vertical-align: top;
	        margin-right:4%;
	    }
	    .article-title-container {
	        display:inline-block;
	        width:70%;
	        vertical-align: top;
	    }
	}
}

/* Extra Small Devices, Phones */ 
@media only screen and (min-width : 480px) {
	.feature-image {
		h1 {
			line-height: 38px;
		}
	}
	.social-sharing {
		height:40px;
		.cf_widget .cf_socialicons .cf_socialicon, .rss-button {
			width:40px !important;
			height: 40px !important;
		}
		.rss-button {
			left:126px;
		}
		.cf_linkedin {	
			left:84px;
		}
