		}
	}
	.article-content {
		margin-top:-150px;
	}
	.featured-post {
		height:400px;
		.text {
			width:45%;
		}
	}
	.content {
		padding:80px 0;
	}
	.col-padding {
		padding:80px 15px;
	}
	.social-sharing {
		.cf_widget .cf_socialicons .cf_socialicon, .rss-button {
			width:116px !important;
		}
		.rss-button {
			left:354px;
		}
		.cf_linkedin {	
			left:236px;
		}
		.cf_twitter {
			left:118px;
		}
		.cf_linkedin:before, .cf_twitter:before, .rss-button:before {
			left: 50px;
		}
		.cf_facebook:before {
			left: 52px;
		}
	}
	.entry-meta {
		float:left;
		width:170px;
		margin:0 15px 15px 0;
		padding:15px 0 0 0 ;
		ul {
			li {
				text-align: center;
				margin-bottom:10px;
			}			
			.categories {
				font-weight: $font-semi-bold;
			}
			.headshot {
				margin-bottom:5px;
			}
			.author {
				font-style: italic;				
			}
		}
	}
}

/* Medium Devices, Desktops */
@media only screen and (min-width : 992px) {
	.social-sharing {
		.cf_widget .cf_socialicons .cf_socialicon, .rss-button {
			width:152px !important;
		}
		.rss-button {
			left:462px;
		}
		.cf_linkedin {	
			left:308px;
		}
		.cf_twitter {
			left:154px;
		}
		.cf_linkedin:before, .cf_twitter:before, .rss-button:before {
			left: 70px;
		}
		.cf_facebook:before {
			left: 72px;
		}
	}
}

/* Large Devices, Wide Screens */
@media only screen and (min-width : 1200px) {
	.feature-image {
		h1 {
			line-height: 52px;
		}
	}
	.social-sharing {
		.cf_widget .cf_socialicons .cf_socialicon, .rss-button {
			width:184px !important;
		}
		.rss-button {
			left:558px;
		}
		.cf_linkedin {	
			left:372px;
		}
		.cf_twitter {
			left:186px;
		}
		.cf_linkedin:before, .cf_twitter:before, .rss-button:before {
			left: 85px;
		}
		.cf_facebook:before {
			left: 87px;
		}
	}
}


button.alm-load-more-btn{	
font-family: Helvetica, Arial, sans-serif;
font-size:16px;
font-weight: 600;
width:auto;   
height: 42px;
line-height: 44px;
background: $peter-river;
color:#fff;
border: none;
margin: 0 0 4px;
padding:0 20px;
display: inline-block;		
position: relative;
-webkit-transition: padding 0.25s ease-in-out, width 0.25s ease-in-out;
-moz-transition: padding 0.25s ease-in-out, width 0.25s ease-in-out;
transition: padding 0.25s ease-in-out, width 0.25s ease-in-out;
text-align: center;	
text-decoration: none;
-webkit-appearance: none; 
-moz-appearance: none;   
appearance: none;      
-webkit-touch-callout: none;
-webkit-user-select: none;
-moz-user-select: -moz-none;
-ms-user-select: none;
user-select: none;
cursor: pointer;
}
button.alm-load-more-btn:hover{
background-color: darken($peter-river, 5%);
color: #fff;
text-decoration: none;
}
button.alm-load-more-btn:active{
-webkit-box-shadow: inset 0 2px 1px rgba(0, 0, 0, 0.1);
-moz-box-shadow: inset 0 2px 1px rgba(0, 0, 0, 0.1);
box-shadow: inset 0 2px 1px rgba(0, 0, 0, 0.1);
text-decoration: none;
}	

/* Loading */
button.alm-load-more-btn.loading{
padding-left: 44px;
}

/* Loaded / Done */
button.alm-load-more-btn.done{
cursor: default;
opacity: 0.2;
filter: alpha(opacity=20); 
background-color: $asbestos; 
-webkit-box-shadow: none !important;
-moz-box-shadow: none !important;
box-shadow: none !important;
}  

/* Loading Icon */
button.alm-load-more-btn:before,
button.alm-load-more-btn.done:before{                
background: none;
width: 0;
}

/* Loading :before */
button.alm-load-more-btn.loading:before {
background: #fff url("../images/ajax-loader.gif") no-repeat center center;
width: 30px;
height: 30px;
margin: 6px;		
-webkit-border-radius:3px;
-moz-border-radius:3px;
border-radius:2px;
display: inline-block;
z-index: 0;
content: '';
position: absolute;
left: 0;
top: 0;
overflow: hidden;
-webkit-transition: width 0.5s ease-in-out;
-moz-transition: width 0.5s ease-in-out;
transition: width 0.5s ease-in-out;
}

.alm-btn-wrap{
text-align: center;
padding: 10px 10px 30px;
overflow: hidden;
display: block;
width:100%;
}

/* Infinite */
.ajax-load-more-wrap.infinite button.alm-load-more-btn {
width: 100%;
background-color: transparent !important; 
-webkit-box-shadow: none !important;
-moz-box-shadow: none !important;
-o-box-shadow: none !important;
-ms-box-shadow: none !important;
box-shadow: none !important;
overflow: hidden;
text-indent: -9999px; 
cursor: default !important;
outline: none !important;
}
.ajax-load-more-wrap.infinite button.alm-load-more-btn:before {
display: none !important;  
}
.ajax-load-more-wrap.infinite button.alm-load-more-btn.done {
opacity: 0;
filter: alpha(opacity=0); 
}   
.ajax-load-more-wrap.infinite button.alm-load-more-btn:active{
-webkit-box-shadow: none;
-moz-box-shadow: none;
box-shadow: none;
}	

.ajax-load-more-wrap.infinite button.alm-load-more-btn {
background-color: transparent;
background-position: center center;
background-repeat: no-repeat;
background-image: url("../../core/img/spinner.gif");   	
border: none !important;
opacity: 0;
filter: alpha(opacity=0); 
}
.ajax-load-more-wrap.infinite button.alm-load-more-btn {      
-webkit-transition: opacity 0.25s ease;
-moz-transition: opacity 0.25s ease;
transition: opacity 0.25s ease;
}
.ajax-load-more-wrap.infinite button.alm-load-more-btn.loading {
opacity: 1;
filter: alpha(opacity=100);  
}


.blog-list {
	margin: 0;
	padding: 0;
	li {
		border:$light-grey-4 solid 1px;
		margin:0 0 5px 0 !important;
		display:table;
		width:100%;
		padding:15px;
		line-height: normal;
		.thumb, .text {
			display:table-cell;
			vertical-align: top;
		}
		.thumb {
			width:100px;
		}
		.text {
			padding:0 30px;
			p {
				margin-bottom:5px;
			}
			a {
				color:$black;
			}
			a:hover {
				text-decoration: underline;
			}
			dl.meta {
				display: table;
				margin:0;
				dd, dt {
					display: table-cell;
				}
				dd {
					padding:0 10px;
					span {
						font-weight: $font-semi-bold;
					}
				}
			}
		}
		.cta-button {
			text-align: right;
			white-space: nowrap;
			font-size: 14px;
		}
	}
}